* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    /* font-family: Arial, Helvetica, sans-serif; */
    line-height: 1.7;
    /* background-color: white; */
    background: url("https://www.teahub.io/photos/full/14-146559_digital.jpg");
    color: #333;
    
  }

  
  .form-group,
  .form-group label,
  .form-group input {
    width: 100%;
    margin-bottom: 0.5rem;
  }
  
  .form-group label {
    font-size: 0.8rem;
  }
  
  .form-group input {
    padding: 10px 20px;
    border: none;
    border-bottom: 3px solid transparent;
    background-color: #eee;
    outline-width: 0;
    font-size: 1rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  /* .form-group input:focus {
    border-bottom: 3px solid rgb(116, 209, 132);
  }
   */
  .btn {
    padding: 10px 20px;
    cursor: pointer;
    width: 100%;
    font-size: 1.2rem;
    border: none;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  .btn:hover {
    opacity: 0.8;
  }
  
  .btn-primary {
    background-color: rgb(5, 5, 5);
    color: #fff;
  }
  
  .error-message {
    width: 100%;
    display: inline-block;
    padding: 5px;
    background: red;
    color: #fff;
    text-align: center;
    margin: 0.5rem 0;
  }
  
  .success-message {
    width: 100%;
    display: inline-block;
    padding: 5px;
    background: green;
    color: #fff;
    text-align: center;
    margin: 0.5rem 0;
  }

.navbar {
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: white;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }

  .menu-bars {
    margin-top: -10.1%;
    text-decoration: none;
    color: white;
    /* margin-left: 170vh; */
    /* width: 1px;
    height: 1px; */
    padding-left: 87%;
    font-size: 10px;
    font-weight: 700;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }

  .nav-menu {
    /* margin-top: 20px; */
    background-color: #94d4d6;
    width: 100px;
    height: 19vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    right: -100%;
    transition: 850ms;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  .nav-menu.active {
    right: 0;
    margin-top: 20%;
    transition: 350ms;
    border-left: 10px black;
  }
  
  .nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    /* padding: 8px 0px 8px 16px; */
    list-style: none;
    height: 60px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  .nav-text a {
    text-decoration: none;
    color: white;
    font-size: 15px;
    width: 100%;
    height: 100%;
    display: flex;
    margin-top: -2.5px;
    padding-left: 9px;
    align-items: center;
    /* border-radius: 4px; */
  }
  
  .nav-text a:hover {
    height: 57px;
    width: 300px;
    background-color: black;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
