* {
  /* box-sizing: border-box; */
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.login-screen {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .login-screen__form {
    width: 380px;
    padding: 1.5rem;
    box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.2);
    background: #fff;
    border-radius: 5px;
  }

  .login-screen__title {
    text-align: center;
    margin-bottom: 1rem;
    font-size: 2rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  div.form-group:nth-child(2) > label:nth-child(1), div.form-group:nth-child(3) > label:nth-child(1){
    font-size: 1rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  .login-screen__forgotpassword {
    font-size: 0.9rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  .login-screen__subtext {
    font-size: 1rem;
    display: block;
    margin: 0.5rem 0;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  a{
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 1rem;
  }

.register-screen {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
